import { useEffect, useState } from "react";
import {
  AuthError,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signOut,
  User,
  UserCredential,
} from "firebase/auth";
import { auth } from "../../../config/firebase";
import { authApi, UserSummaryDto } from "../../../api/client";

// Firebase 
const isFirebaseError = (error: unknown): error is AuthError => {
  return (
    typeof error === "object" &&
    error !== null &&
    "code" in error &&
    typeof (error as AuthError).code === "string"
  );
};

interface AuthState {
  firebaseUser: User | null;
  appUser: UserSummaryDto | null;
  loading: boolean;
  error: string | null;
  isAuthenticated: boolean;
}

interface LoginCredentials {
  email: string;
  password: string;
}

export const useAuth = () => {
  const [authState, setAuthState] = useState<AuthState>({
    firebaseUser: null,
    appUser: null,
    loading: true,
    error: null,
    isAuthenticated: false,
  });

  // Firebase
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async firebaseUser => {
      if (firebaseUser) {
        try {
          // FirebaselocalIduidlocalId
          const localId = firebaseUser.uid;

          // localId
          const backendResponse = await authApi.login(localId);

          if (backendResponse.success && backendResponse.user) {
            // : FirebaseBackendOK
            setAuthState({
              firebaseUser,
              appUser: backendResponse.user,
              loading: false,
              error: null,
              isAuthenticated: true,
            });
          } else {
            // Backend: Firebase
            // eslint-disable-next-line no-console
            console.warn(
              "Backend authentication failed, logging out from Firebase"
            );
            await signOut(auth);
            setAuthState({
              firebaseUser: null,
              appUser: null,
              loading: false,
              error: backendResponse.error || "",
              isAuthenticated: false,
            });
          }
        } catch (error) {
          // eslint-disable-next-line no-console
          console.error("Backend authentication error:", error);
          // Firebase
          await signOut(auth);
          setAuthState({
            firebaseUser: null,
            appUser: null,
            loading: false,
            error: "",
            isAuthenticated: false,
          });
        }
      } else {
        // Firebase
        setAuthState({
          firebaseUser: null,
          appUser: null,
          loading: false,
          error: null,
          isAuthenticated: false,
        });
      }
    });

    return () => unsubscribe();
  }, []);

  // 
  const login = async (
    credentials: LoginCredentials
  ): Promise<{ success: boolean; error?: string }> => {
    setAuthState(prev => ({ ...prev, loading: true, error: null }));

    try {
      // 1. Firebase
      const userCredential: UserCredential = await signInWithEmailAndPassword(
        auth,
        credentials.email,
        credentials.password
      );

      // 2. localIduidlocalId
      const localId = userCredential.user.uid;

      // 3. localId
      const backendResponse = await authApi.login(localId);

      if (backendResponse.success && backendResponse.user) {
        // 
        setAuthState({
          firebaseUser: userCredential.user,
          appUser: backendResponse.user,
          loading: false,
          error: null,
          isAuthenticated: true,
        });

        return { success: true };
      } else {
        // Backend: Firebase
        await signOut(auth);
        const errorMessage =
          backendResponse.error || "";
        setAuthState(prev => ({
          ...prev,
          loading: false,
          error: errorMessage,
          isAuthenticated: false,
        }));
        return { success: false, error: errorMessage };
      }
    } catch (error: unknown) {
      let errorMessage = "";

      if (isFirebaseError(error)) {
        errorMessage = getErrorMessage(error.code);
      } else if (error instanceof Error) {
        // eslint-disable-next-line no-console
        console.error("Login error:", error.message);
        errorMessage = "";
      }

      setAuthState(prev => ({
        ...prev,
        loading: false,
        error: errorMessage,
        isAuthenticated: false,
      }));
      return { success: false, error: errorMessage };
    }
  };

  // 
  const logout = async (): Promise<{ success: boolean; error?: string }> => {
    try {
      // Firebase (Firebase)
      await signOut(auth);
      setAuthState({
        firebaseUser: null,
        appUser: null,
        loading: false,
        error: null,
        isAuthenticated: false,
      });
      return { success: true };
    } catch {
      const errorMessage = "";
      setAuthState(prev => ({
        ...prev,
        error: errorMessage,
      }));
      return { success: false, error: errorMessage };
    }
  };

  // 
  const getErrorMessage = (errorCode: string): string => {
    switch (errorCode) {
      case "auth/user-not-found":
        return "";
      case "auth/wrong-password":
        return "";
      case "auth/invalid-email":
        return "";
      case "auth/user-disabled":
        return "";
      case "auth/too-many-requests":
        return "";
      case "auth/network-request-failed":
        return "";
      case "auth/invalid-credential":
        return "";
      default:
        return "";
    }
  };

  return {
    firebaseUser: authState.firebaseUser,
    user: authState.appUser, // 
    loading: authState.loading,
    error: authState.error,
    isAuthenticated: authState.isAuthenticated,
    login,
    logout,
  };
};
